import React from 'react';
import './App.css';
import {ShopCards} from "./components/ShopCards";
import {BasketItem} from "./components/BasketItem";
import {
    Navbar,
    Nav,
    NavItem,
    NavLink,
} from 'reactstrap';

export function App(props) {

    return (
        <div className="App">
            <Navbar fixed="top" dark expand="md" className="main-navbar">
                <Nav navbar>
                    <div className="placeholder"></div>
                    <div className="cv-text">CV</div>
                    <NavItem>
                        <BasketItem/>
                    </NavItem>
                </Nav>
            </Navbar>

            <div className="header">Laura Herma</div>
            <Navbar light className="secondary-navbar">
                <Nav>
                    <NavLink>Portfolio pood</NavLink>
                    <NavLink href="https://github.com/reactstrap/reactstrap">GitHub</NavLink>
                    <NavLink>Minust</NavLink>
                </Nav>
            </Navbar>
            <ShopCards/>
        </div>
    );
}


export default App;



