import {basket} from "../data/basketData";
import React from 'react';
import {observer} from 'mobx-react';

export const BasketItem = observer(() => {
    const hasItemsInBasket = () => {
        if (basket.length) {
            return <div className="basket-icon-text">{basket.length}</div>
        }
    };


    return <div>
        <div>
            <i className="material-icons basket-icon">shopping_cart</i>
            {hasItemsInBasket()}
        </div>
    </div>
});


